import React, { PropsWithChildren, ReactElement, DetailedHTMLProps, SelectHTMLAttributes } from 'react';
import styled from 'styled-components/macro';

import { Icon } from '../Icon';

type SelectProps = PropsWithChildren & DetailedHTMLProps<SelectHTMLAttributes<HTMLSelectElement>, HTMLSelectElement> & { label: string }

export const Select = ({ label, value, children, ref, ...delegated }: SelectProps) => {
  const childArray = React.Children.toArray(children);
  const selectedChild = childArray.find(
    (child) => isSelected(child as ReactElement, value as string)
  ) as ReactElement;

  const displayedValue = selectedChild.props.children;

  return (
    <Wrapper>
      <VisibleLabel>{label}</VisibleLabel>
      <SelectWrapper>
        <NativeSelect {...delegated}>{children}</NativeSelect>

        <DisplayedBit>
          {displayedValue}
          <ChevronIcon
            id="chevron-down"
            size={24}
            strokeWidth={1.5}
          />
        </DisplayedBit>
      </SelectWrapper>
    </Wrapper>
  );
};

function isSelected (el: ReactElement, value: string) {
  return el.props.value === value
}

const Wrapper = styled.label`
display: flex;
align-items: baseline;
`;

const VisibleLabel = styled.span`
  color: var(--gray-700);
  margin-right: ${16 / 16}rem;
`;

const SelectWrapper = styled.div`
  position: relative;
`;

const NativeSelect = styled.select`
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
`;

const DisplayedBit = styled.span`
  display: block;
  background: var(--gray-100);
  font-size: 1rem;
  font-weight: var(--font-weight-medium);
  color: var(--gray-900);
  padding: 12px 42px 12px 16px;
  border-radius: 8px;
  pointer-events: none;

  ${NativeSelect}:focus ~ & {
    outline: 1px dotted #212121;
    outline: 5px auto -webkit-focus-ring-color;
  }
`;

const ChevronIcon = styled(Icon)`
  position: absolute;
  top: 0;
  right: 9px;
  bottom: 0;
  margin: auto;
  width: 24px;
  height: 24px;
`;

